import template from './multi-steps.html';

function MultiStepsDirective() {
  /*ngInject*/

  return {
    restrict: 'E',
    scope: {
      currentIndex: '=',
    },
    template: template,

    controller: MultiStepsController,
    controllerAs: 'vm',
    bindToController: true,
    transclude: true,
  };

}

class MultiStepsController {
  constructor($scope) {
    'ngInject';

    this.steps = [];
    this.currentIndex = 1;
    this.$scope = $scope;
    this.activate();
  }

  activate() {
    this.$scope.$watch('vm.currentIndex', value => {
      if (typeof (value) !== 'number' || value > this.steps.length) {
        this.currentIndex = 1;
        return;
      }

      let currentStep = this.steps[value - 1];
      this.activeStep(currentStep);

    });
  }

  activeStep(step) {
    this.steps.forEach(stepScope => {
      if (stepScope !== step) {
        stepScope.isActive = false;
      }
    });
    step.isActive = true;
    this.currentIndex = this.steps.indexOf(step) + 1;
  }

  addStep(stepScope) {
    this.steps.push(stepScope);

    if (!stepScope.index) {
      stepScope.index = this.steps.indexOf(stepScope) + 1;
      if (stepScope.index === 1) {
        stepScope.isActive = true;
      }
    }

    stepScope.$on('$destroy', event => {
      this.removeStep(stepScope);
    });

  }

  removeStep(step) {
    var index = this.steps.indexOf(step);
    if (index !== -1) {
      this.steps.splice(index, 1);
    }
  }
}

export default MultiStepsDirective;




